<template>
	<ul class="wg-ordered-config-comp">
		<li
            v-for="(item, idx) in data.items" :key="item"
			@dragenter="dragEnter"
			@dragleave="dragLeave"
			@dragover="dragOver"
			@drop="dragDrop"
            :data-index="idx"
		>
			<div
				class="dragable"
				draggable="true"
				@dragstart="dragStart"
				@mousedown="dragPush"
			>
				<div class="order-body">
                    <slot :key="idx" :param="item" />
                </div>
				<div class="order-control">
					<button class="wg-button-wrapper" @click="onDelete(item)">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							fill="currentColor"
							height="24"
							viewBox="0 -960 960 960"
							width="24"
						>
							<path
								d="M280-120q-33 0-56.5-23.5T200-200v-520h-40v-80h200v-40h240v40h200v80h-40v520q0 33-23.5 56.5T680-120H280Zm400-600H280v520h400v-520ZM360-280h80v-360h-80v360Zm160 0h80v-360h-80v360ZM280-720v520-520Z"
							/>
						</svg>
					</button>
					<button class="wg-button-wrapper ordering">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							fill="currentColor"
							height="24"
							viewBox="0 -960 960 960"
							width="24"
						>
							<path
								d="M120-280v-80h560v80H120Zm0-160v-80h560v80H120Zm0-160v-80h560v80H120Zm680 320q-17 0-28.5-11.5T760-320q0-17 11.5-28.5T800-360q17 0 28.5 11.5T840-320q0 17-11.5 28.5T800-280Zm0-160q-17 0-28.5-11.5T760-480q0-17 11.5-28.5T800-520q17 0 28.5 11.5T840-480q0 17-11.5 28.5T800-440Zm0-160q-17 0-28.5-11.5T760-640q0-17 11.5-28.5T800-680q17 0 28.5 11.5T840-640q0 17-11.5 28.5T800-600Z"
							/>
						</svg>
					</button>
				</div>
			</div>
		</li>
	</ul>
</template>

<style scoped>
	@import "./components.css";
	.wg-ordered-config-comp {
		margin-top: 20px;
		position: relative;
	}

	.wg-ordered-config-comp li {
		box-shadow: none;
        margin-top: 5px;
        padding-bottom: 0px;
	}

	.wg-ordered-config-comp li .dragable {
		box-shadow: none;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		list-style: none;
        gap: 8px;
	}

    .wg-ordered-config-comp li .order-body{
        height: 100%;
        flex: 1 1 100%;
        padding: 0 2px;
    }
    .wg-ordered-config-comp li .order-body.over{
        background-color: #e4e4e4de;
    }
    .wg-ordered-config-comp li .dragable .order-control{
        flex: 0 0 80px;
    }
	.wg-ordered-config-comp li .dragable .order-control button + button {
		margin-left: 10px;
	}
</style>

<script lang="ts">
import { onMounted, PropType, reactive, toRaw, watch } from 'vue';

export interface SortItemID{
    itemID: number|null,
    itemName: string
}

interface dataReactive{
    items: SortItemID[]
}

export default {
    setup(props, ctx) {
        let index: number = -1;
        let eventTarget: HTMLElement | null = null;
        let data = reactive<dataReactive>({
            items: []
        });
        const dragStart = (e: Event) => {
            const el = e.currentTarget as HTMLElement;
            if (el.getElementsByClassName("ordering")[0].contains(eventTarget)) {
                index = parseInt(el.parentElement!.getAttribute("data-index")!);
            } else {
                e.preventDefault();
            }
        };

        const dragEnter = (e: Event) => {
            (e.currentTarget as HTMLElement)
                .getElementsByClassName("order-body")[0]
                .classList.add("over");
        };

        const dragLeave = (e: Event) => {
            (e.currentTarget as HTMLElement)
                .getElementsByClassName("order-body")[0]
                .classList.remove("over");
        };

        const dragOver = (e: Event) => {
            e.preventDefault();
        };

        const dragDrop = (e: Event) => {
            e.preventDefault();
            console.log("drag drop");
            
            (e.currentTarget as HTMLElement)
                .getElementsByClassName("order-body")[0]
                .classList.remove("over");
            swap(index, parseInt((e.currentTarget as HTMLElement).getAttribute("data-index")!));
        };

        const dragPush = (e: Event) => {
            eventTarget = e.target as HTMLElement;
        };
        const swap = (fromIdx:number, toIdx:number)=>{
            console.log("data.items ", data.items);
            
            let temp = data.items[fromIdx];
            data.items[fromIdx] = data.items[toIdx];
            data.items[toIdx] = temp;
            ctx.emit("update:modelValue", toRaw(data.items));
        }
        const onDelete = (item:any)=>{
            ctx.emit("delete", toRaw(item));
        }

        onMounted(()=>{
            data.items = toRaw(props.modelValue)!;
        })
        watch(props.modelValue!, ()=>{
            data.items = toRaw(props.modelValue)!;
        });

        return {
            dragStart,
            dragEnter,
            dragLeave,
            dragOver,
            dragDrop,
            dragPush,
            onDelete,
            data
        };
    },
    props: {
        modelValue: {
            type: Object as PropType<SortItemID[]>
        }
    }
};
</script>
