<template>
	<div class="wgt-drpc-container">
		<div class="wgt-drpc-input-wrap">
			<label for="wgt-drpc-id" class="wgt-drpc-label" v-if="dropdownTitle != ''">{{dropdownTitle}}</label>
			<input
				type="text"
				readonly
				id="wgt-drpc-id"
				class="wgt-drpc-input"
				placeholder="Pilih Device"
				:value="data.selectItem"
			/>
			<span class="icon-input"
				><svg
					fill="currentColor"
					xmlns="http://www.w3.org/2000/svg"
					height="24"
					viewBox="0 -960 960 960"
					width="24"
				>
					<path d="M480-360 280-560h400L480-360Z" /></svg
			></span>
			<div class="clickable" @click="onShow" ref="inputEl"></div>
		</div>
		<div class="wgt-drpc-search-wrap" ref="searchEl">
			<ul class="wgt-drpc-search" v-if="data.onLoad" @scroll="onScroll">
				<li
					v-for="(item, idx) in items.value"
					:key="idx"
					@click="onClick(item.id)"
				>
					{{ item.name }}
				</li>
			</ul>
		</div>
	</div>
</template>

<style >
	.border-loader::before {
		width: 100%;
		height: 3px;
		display: block;
		content: " ";
		background: linear-gradient(currentColor 0 0) 0 100%/0% 3px no-repeat;
		position: absolute;
		z-index: 30;
		animation: l2 2s linear infinite;
	}
	@keyframes l2 {
		to {
			background-size: 100% 3px;
		}
	}
</style>
<style scoped>
	.wgt-drpc-container {
		background-color: #fcdb9e;
		border-top-left-radius: 6px;
		border-top-right-radius: 6px;
	}
	.wgt-drpc-label {
		display: inline-block;
		font-size: 14px;
		color: #f8a710;
		padding-left: 10px;
		font-weight: 500;
		cursor: inherit;
		background-color: transparent;
	}
	.wgt-drpc-input {
		display: block;
		width: 100%;
		outline: none;
		border: none;
		padding: 2px 10px 10px 10px;
		background-color: transparent;
		cursor: default;
		font-weight: 400;
		color: #0b11337d;
		cursor: inherit;
	}
	.wgt-drpc-input-wrap {
		padding-top: 9px;
		cursor: pointer;
	}
	.wgt-drpc-input-wrap,
	.wgt-drpc-search-wrap {
		position: relative;
	}
	.wgt-drpc-search-wrap {
		color: #faa735;
	}
	.clickable {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
	.icon-input {
		position: absolute;
		right: 12px;
		top: 50%;
		transform: translateY(-50%);
	}
	.wgt-drpc-search {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 10;
		width: 100%;
	}
	.wgt-drpc-search {
		background-color: #fff;
		max-height: 330px;
		overflow-y: auto;
	}
	.wgt-drpc-search li {
		background-color: #fff;
		box-shadow: none;
		margin: 0;
		padding: 18px 12px;
		cursor: pointer;
		color: rgb(151, 151, 151);
	}
	.wgt-drpc-search li:hover {
		background-color: rgb(245, 245, 245);
	}
</style>

<script lang="ts">
	import {
		defineComponent,
		onMounted,
		onUnmounted,
		PropType,
		reactive,
		ref,
	} from "vue";

	interface dr {
		onLoad: Boolean;
		selectItem: string;
	}
	export interface DropdownItem {
		id: number;
		name: string;
	}
	export interface DropdownProp {
		value: DropdownItem[];
	}
	export default defineComponent({
		setup(props, { emit }) {
			const searchEl = ref<HTMLElement>();
			const inputEl = ref<HTMLElement>();
			const data = reactive<dr>({
				onLoad: false,
				selectItem: "",
			});

			const onScroll = (e: Event) => {
				if (
					(e.currentTarget as HTMLElement).scrollTop +
						(e.currentTarget as HTMLElement).clientHeight >=
					(e.currentTarget as HTMLElement).scrollHeight
				) {
					emit("nextBatch");
				}
			};
			const resetLoadState = (e: Event) => {
				if (!inputEl.value?.contains(e.target as Node)) {
					data.onLoad = false;
				}
			};
			const onShow = (e: Event) => {
				data.onLoad = !data.onLoad;
			};
			const onClick = (id: number) => {
				const df = props.items?.value.filter((v) => v.id == id);
				if (df && df?.length! > 0) {
					data.selectItem = df[0].name as string;
				} else {
					data.selectItem = "";
				}

				emit("selectItem", id, data.selectItem);
			};

			onMounted(() => {
                if(props.defaultValue){
                    data.selectItem = props.defaultValue as string;
                }
				window.addEventListener("click", resetLoadState);
			});
			onUnmounted(() => window.removeEventListener("click", resetLoadState));
			return {
				onScroll,
				onShow,
				onClick,
				data,
				searchEl,
				inputEl,
			};
		},
		props: {
			items: {
				type: Object as PropType<DropdownProp>,
			},
            defaultValue: {
                type: Object as PropType<String>
            },
            dropdownTitle: {
                type: Object as PropType<String>,
                default: ""
            }
		},
	});
</script>
