<script lang="ts">
	import { onMounted, onUnmounted, PropType, reactive } from "vue";

	export interface TableDataDetail {
		title: string;
		description: string;
		avatarUrl: string;
		ID: number;
		hidden: boolean;
	}
	export interface TableDataPropModel {
		data: TableDataDetail[];
	}

	interface dataReactive{
		data: TableDataDetail[][];
	}
	export default {
		props: {
			tableDataModel: {
				type: Object as PropType<TableDataPropModel>,
				required: true,
			},
		},
		setup(props, ctx) {
			let dr:dataReactive = reactive<dataReactive>({data:[]})

			const hidenItem:TableDataDetail = {
				avatarUrl: "",
				description: "",
				ID: -1,
				title: "",
				hidden: true,
			};

			let countingItems = ()=>{
				const wz = window.innerWidth
				let maxCols = 0
				if (wz >= 420 && wz < 712){
					maxCols = 2;
				}else if (wz >= 712 && wz < 960){
					maxCols = 3;
				}else if(wz >= 960){
					maxCols = 4;
				}
				dr.data = []
				if(maxCols > 0){
					let l = 0;
					while(l < props.tableDataModel.data.length){
						let detail:TableDataDetail[] = []
						for(let i=0;i<maxCols;i++){
							if(l < props.tableDataModel.data.length){
								detail.push(props.tableDataModel.data[l])
							}else{
								detail.push(hidenItem)
							}	
							l++;
						}
						dr.data.push(detail)
					}
				}
			}
			onMounted(()=>{
				countingItems();
				window.addEventListener('resize', countingItems);
			})
			onUnmounted(()=>{window.removeEventListener('resize', countingItems);})

			let onSelectItem = (id: string) => {
				ctx.emit("selectItem", id);
			};

			return {
				onSelectItem,
				dr
			};
		},
	};
</script>
