<template>
	<BaseConfig @cancel="cancel" widgetName="Peta Device">
		<ul class="wgt-config-list">
			<li>
				<h4 class="title">Map Datasource</h4>
				<div class="wrap-list">
					<SortableComp v-model="data.sortParam" @delete="onDelete" v-slot="{param}">
						<DropdownSelectorComp :items="data.dropdownProp" @selectItem="(id, name)=>{param.itemID = id; param.itemName = name;}" dropdownTitle="Device" :defaultValue="param.defaultValue"/>
					</SortableComp>
				</div>
				<button class="wg-button-wrapper" @click="addDataSource">
					Tambah datasource
				</button>
			</li>
		</ul>
	</BaseConfig>
</template>

<style scoped>
	@import "./configs.css";
	@import "../../components/components.css";
	.wg-button-wrapper {
		color: #f5a30c;
		padding-left: 0;
		margin-top: 12px;
	}
	.wg-button-wrapper:hover {
		background-color: #f0f0f0;
		color: #f5a30c;
	}
	.wgt-config-list .title {
		color: #4d4d4d;
		font-weight: 500;
	}
	.wgt-config-list .wrap-list {
		min-height: 400px;
		max-height: 500px;
		overflow-y: auto;
	}
</style>

<script lang="ts">
	import { reactive, watch } from "vue";
	import BaseConfig from "./BaseConfigComp.vue";
	import SortableComp, { SortItemID } from "@/components/SortableComp.vue";
	import DropdownSelectorComp, {
		DropdownProp,
	} from "@/components/DropdownSelectorComp.vue";

	interface dataReactive {
		sortParam: SortItemID[];
		dropdownProp: DropdownProp;
	}
	export default {
		setup(props, ctx) {
			const data = reactive<dataReactive>({
				sortParam: [
					{
						itemID: null,
                        itemName: "Device Z"
					},
				],
				dropdownProp: {
					value: [
						{ id: 0, name: "Device A" },
						{ id: 1, name: "Device B" },
						{ id: 2, name: "Device C" },
					],
				},
			});
			watch(data.sortParam, ()=>{
				data.sortParam	
			})
			
			const onDelete = (item: any) => {
				console.log("delete ", item);
			};
			const cancel = () => {
				ctx.emit("cancel");
			};
			const addDataSource = () => {
				data.sortParam.push({
					itemID: null!,
                    itemName:""
				});
			};
			return {
				data,
				onDelete,
				addDataSource,
				cancel,
			};
		},
		components: { BaseConfig, SortableComp, DropdownSelectorComp },
	};
</script>
