<template>
	<div class="wg-main-layout">
		<WidgetSelector
			@change="widgetSelectorEvent"
			@cancelEdit="onCancel"
			:layoutName="layoutName"
		/>
		<div
			style="height: 100%"
			:style="{
				paddingTop: !data.widgetSelectorEvent.showSelector ? '50px' : '0',
			}"
		>
			<gridster :options="data.options" v-slot="slotProps">
				<gridItem
					v-for="widget in data.widgets"
					:key="widget"
					:gridster="slotProps.gridster"
					:item="widget"
					:event="data.event"
				>
				</gridItem>
			</gridster>
		</div>
	</div>
</template>

<style scoped>
	.wg-main-layout {
		width: 100;
		height: 100%;
		position: relative;
		border: 0;
		margin: 0;
		padding: 0;
		box-sizing: border-box;
	}
</style>

<script lang="ts">
	import { onMounted, onUnmounted, reactive } from "vue";
	import gridster from "@/grid/gridster.vue";
	import gridItem from "@/grid/grid-item.vue";
	import WidgetSelector, { widgetEvent } from "./WidgetSelector.vue";
	import { DisplayGrid, GridsterConfig } from "@/grid/gridsterConfig.interface";
	import { GridsterItem } from "@/grid/gridsterItem.interface";
	import { Subject } from "rxjs";
	import { Utils } from "@/Utils";

	interface mainLayoutReactive {
		widgets: GridsterItem[];
		options: GridsterConfig;
		widgetSelectorEvent: widgetEvent;
		event: Subject<void>;
	}

	export default {
		setup() {
			let tempWidget = [
				{ cols: 2, rows: 2, y: 0, x: 0, d: "A" },
				{ cols: 2, rows: 2, y: 0, x: 1, d: "B" },
				{ cols: 2, rows: 2, y: 0, x: 2, d: "C" },
			];

			const data = reactive<mainLayoutReactive>({
				widgets: Object.assign({}, tempWidget),
				options: Utils.defaultGridOptions(),
				widgetSelectorEvent: {
					showSelector: false,
					onEdit: false,
				},
				event: new Subject<void>(),
			});
			let widgetSelectorEvent = (e: widgetEvent) => {
				data.options.resizable!.enabled = data.options.draggable!.enabled =
					e.onEdit;
				data.widgetSelectorEvent = e;
			};

			onMounted(() => {
				data.event = new Subject<void>();
			});

			onUnmounted(() => {
				data.event = null!;
			});

			let onCancel = () => {
				data.event.next();
			};

			return {
				data,
				widgetSelectorEvent,
				onCancel,
			};
		},
		components: { gridItem, gridster, WidgetSelector },
		props: {
			layoutName: {
				type: String,
			},
		},
	};
</script>
