import { Subscription } from "rxjs";
import { EventEmitter, Renderer2 } from "./angular.interface";
import { GridsterItem } from "./gridsterItem.interface";
import { GridsterRenderer } from "./gridsterRenderer.service";

export class GridPreviewComponent {
    previewStyle$: EventEmitter<GridsterItem | null>;
    gridRenderer: GridsterRenderer;
    renderer : Renderer2;
    el:HTMLElement;
    sub: Subscription;

    constructor(preview: EventEmitter<GridsterItem | null>, gridRenderer: GridsterRenderer,renderer : Renderer2,el:HTMLElement){
        this.gridRenderer = gridRenderer;
        this.previewStyle$ = preview;
        this.renderer = renderer;
        this.el = el;
    }

    init(): void {
        this.sub = this.previewStyle$.subscribe(options =>
            this.previewStyle(options)
        );
    }
    
    destroy(): void {
        if(this.sub) {
        this.sub.unsubscribe();
        }
    }

    private previewStyle(item: GridsterItem | null): void {
        if (item) {
          this.renderer.setStyle(this.el, 'display', 'block');
          this.gridRenderer.updateItem(this.el, item, this.renderer);
        } else {
          this.renderer.setStyle(this.el, 'display', '');
        }
      }
}